import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, Heading, Text, HStack, Link
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../../layout/MiniBox';
import style from './ExecuteSend.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { sendCrypto } from '../../../../store/middlewares/crypto';
import { apiErrorHandler } from '../../../../utils/general';

const ExecuteSend = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();

  const { selectedAccount: cryptoAccount, sendFormData, sendGasFeesData } = useAppSelector((store) => store.crypto, shallowEqual);

  const onSubmit = useCallback(() => {
    if (cryptoAccount && sendFormData) {
      dispatch(sendCrypto({
        walletId: cryptoAccount?.id,
        destinationCryptoAddress: sendFormData.destinationCryptoAddress,
        description: sendFormData.description,
        quantity: sendFormData.quantity,
        cryptoCurrency: sendFormData.cryptoCurrency
      })).then(() => {
        navigate('/crypto/send/success');
      })
        .catch(apiErrorHandler);
    }
  }, [sendFormData, cryptoAccount]);

  return (
    <div className="page">
      <MiniBox
        heading={t('crypto_send_details.title')}
        footer={(
          <Button width="100%" variant="primary" onClick={onSubmit}>
            {t('crypto_send_details.ctaText')}
          </Button>
          )}
      >
        <Box width="100%" paddingTop="15px">
          <Heading data-testid="status-title" variant="primary" textAlign="center">
            {t('crypto_send_details.sending')}
            {' '}
            {sendFormData?.quantity}
            {' '}
            {sendFormData?.cryptoCurrency}
          </Heading>
          <Text className={`${style.desc} text-light`} variant="regular" textAlign="center">
            {t('crypto_send_details.anyQuestions')}
            {' '}
            <Link textDecoration="underline" href="mailto:chat@solidfi.com" style={{ color: 'inherit', textDecoration: 'underline' }} isExternal>chat@solidfi.com</Link>
          </Text>
          <Box className={style.details_label}>
            <HStack className={style.details_value}>
              <Text w="50%" variant="regular" textAlign="left" className="text-light">{t('crypto_send_details.source')}</Text>
              <Text w="50%" variant="regular" textAlign="left">
                {cryptoAccount?.label}
                <br />
                <span>
                  {cryptoAccount?.id}
                </span>

              </Text>
            </HStack>
            <HStack className={style.details_value}>
              <Text w="50%" variant="regular" textAlign="left" className="text-light">{t('crypto_send_details.destination')}</Text>
              <Text w="50%" variant="regular" textAlign="left">{sendFormData?.destinationCryptoAddress}</Text>
            </HStack>
            <HStack className={style.details_value}>
              <Text w="50%" variant="regular" textAlign="left" className="text-light">{t('crypto_send_details.quantity')}</Text>
              <Text w="50%" variant="regular" textAlign="left">{sendFormData?.quantity}</Text>
            </HStack>
            <HStack className={style.details_value}>
              <Text w="50%" variant="regular" textAlign="left" className="text-light">{t('crypto_send_details.fee')}</Text>
              <Text w="50%" variant="regular" textAlign="left">{sendGasFeesData?.estimatedGasFee}</Text>
            </HStack>
            <HStack className={style.details_value}>
              <Text w="50%" variant="regular" textAlign="left" className="text-light">{t('crypto_send_details.description')}</Text>
              <Text w="50%" variant="regular" textAlign="left">{sendFormData?.description}</Text>
            </HStack>

          </Box>
        </Box>
        <Text variant="small" whiteSpace="pre-line" fontSize="9px" textAlign="left">{t('crypto_send_details.poweredBy')}</Text>
      </MiniBox>
    </div>
  );
};

export default ExecuteSend;
